@use '../abstracts/functions' as *;

/* ----------------------------- */
/* COLORS
/* ----------------------------- */

// GRAPHIC CHARTER
$omf-color--primary: #9356DC; 
$omf-color--secondary: #FF79DA;
$omf-color--tertiary: #99E2D0;

// GRAPHIC CHARTER VARIANTS
$omf-color--primary-light: scale-color($omf-color--primary, $lightness: 85%);
$omf-color--tertiary-dark: scale-color($omf-color--tertiary, $lightness: -65%, $saturation: 90%);

// OTHERS
$omf-color--black: #101010;
$omf-color--grey-darkest: #353535;
$omf-color--grey-darker: #7e7e7e;
$omf-color--grey-light: #cecece;
$omf-color--grey-lighter: #eaeaea;
$omf-color--grey-lightest: #f6f6f6;

/* ----------------------------- */
/* FONTS
/* ----------------------------- */

// FONT-FAMILY
$omf-font--special: 'Shrikhand', cursive;
$omf-font--default: 'Roboto', sans-serif;

// FONT-SIZE
$omg-font-size--xlarge: rem(30);
$omg-font-size--large: rem(25);
$omg-font-size--medium: rem(18);
$omg-font-size--small: rem(16);
$omg-font-size--xsmall: rem(14);

/* ----------------------------- */
/* SPACINGS
/* ----------------------------- */
:root {
  --gutter--default: #{rem(20)};
}
@media screen and (min-width: 769px) {
  :root {
    --gutter--default: 10vw;
  }
}
$gutter--default: var(--gutter--default);
$padd--default: rem(20);
$gutter-inner--small: 4vw;
$gutter-inner--medium: 5.33vw;

